@import "../common/style/sass/variables";
@import "../common/style/sass/mixins";

.main {
  height: 100vh;
  background-color: $bgColor;

  .container {
    height: 100%;
    width: 80%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    @media screen and (max-width: 851px) {
      justify-content: center;
    }

    .text {
      background-color: $bgSecondColor;
      width: 360px;
      border: 1px solid $primaryColor;

      span {
        @include text(('Poppins', sans-serif), 14px, 1.8);
        font-weight: 400;
      }

      h1 {
        @include text(('Josefin Sans', sans-serif), 40px, 1.2);
        font-weight: 700;
        padding: 10px 0 10px;
        letter-spacing: 5px;
      }

      p {
        @include text(('Poppins', sans-serif), 27px, 1.8);
        font-weight: 400;
      }
    }

    .photo {
      background-color: $bgColor;
      color: $textColor;
      width: 300px;
      height: 400px;
      border: 1px solid $primaryColor;
      background-image: url("../common/image/me.jpeg");
      background-size: cover;
      background-position: center;
      position: relative;
    }
  }
}
