@import "../../common/style/sass/variables";
@import "../../common/style/sass/mixins";

.btn {
  cursor: pointer;
  color: $textColor;
  background-color: $bgColor;
  @include text(('Poppins', sans-serif), 14px, 1.2);
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 1px;
  padding: 7px 20px;
  max-width: 170px;
  margin: 15px auto;
  text-align: center;
  width: 100%;
  border: none;
  position: relative;
}
