@import "../common/style/sass/variables";
@import "../common/style/sass/mixins";

.main {
    background-color: $bgSecondColor;
    height: 100vh;
    .container {
        height: 100%;
        width: 80%;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        .form {
            background-color: $bgSecondColor;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;
            input {
                margin: 5px;
                border: 1px solid $textColor;
                background-color: $bgColor;
                color: $textColor;
                @include text(('Josefin Sans', sans-serif), 16px, 1.2);
            }
            textarea {
                margin: 5px;
                border: 1px solid $textColor;
                background-color: $bgColor;
                @include text(('Josefin Sans', sans-serif), 16px, 1.2);

            }
        }
    }
}


