@import "../common/style/sass/variables";

.skillBox {
  background-color: $bgSecondColor;
  min-height: 100vh;

  .container {
    height: 100%;
    width: 80%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;

    .skills {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      @media screen and (max-width: 671px) {
        justify-content: center;
      }
    }
  }
}







