@import "../../common/style/sass/variables";
@import "../../common/style/sass/mixins";

.project {
    border: 1px solid $primaryColor;
    background-color: $bgSecondColor;
    width: 250px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin: 5px;
    .imgContainer {
        height: 100px;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        background-image: url("https://keramika-na-dom.ru/files/products/7c3012d9aee2c69d8ccf31cc39abee31.560x560.png?54f847de51d78d9c6cf0fae56b1609ef");
        background-size: cover;
        background-position: center;
        position: relative;
        &::before {
            position: absolute;
            content: '';
            background-color: $bgColor;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            opacity: 0.4;
        }
        .viewBtn {
            background-color: $primaryColor;
            padding: 7px 12px;
            position: relative;
            /*z-index: 1;*/
            color: $textColor;
        }
    }
    .projectInfo{
        padding: 25px 20px;
        .projectTitle {
            @include text(('Josefin Sans', sans-serif), 16px, 1.2);

            letter-spacing: 1px;
            margin: 0 0 5px;
        }
        .descr {
            @include text(('Poppins', sans-serif), 12px, 1.8);
            font-weight: 400;
        }
    }
}
