@import "../common/style/sass/variables";
@import "../common/style/sass/mixins";

.main {
  background-color: $bgColor;
  height: 280px;

  .container {
    height: 100%;
    width: 80%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    @include text(('Poppins', sans-serif), 14px, 1.8);
    color: rgba(255, 255, 255, .5);

    .title {
      background-color: $bgColor;
      @include text(('Josefin Sans', sans-serif), 22px, 1.2);
    }

    .center {
      width: 280px;
      height: 40px;
      display: flex;
      justify-content: space-between;
      border: 1px solid;

      .item {
        width: 40px;
        height: 40px;
        background-color: $textColor;
      }
    }
  }
}







